import { sendMessageToAllTabs } from "../../core/browers/tabs";

export const sidepanelConnections = new Map();

export const listenSidePanelConnected = () => {
    const handlePortDisconnect = (port, windowId) => {
        console.log("port.onDisconnect", port.name);
        sidepanelConnections.delete(port.name);
        sendMessageToAllTabs("sidepanel_closed", { name: port.name, windowId: windowId });
    };

    const handlePortConnect = (port) => {
        if (port.name.startsWith("sidepanel_")) {
            console.log("listenSidePanelConnected onConnect", port.name, port);
            sidepanelConnections.set(port.name, port);
            const windowId = parseInt(port.name.split('_')[1]);
            sendMessageToAllTabs("sidepanel_opened", { name: port.name, windowId: windowId });
            port.onDisconnect.addListener(() => handlePortDisconnect(port, windowId));
        }
    };

    chrome.runtime.onConnect.addListener(handlePortConnect);
};

export const isSidePanelOpen = (windowId) => {
    if (!windowId) return false;
    const open = sidepanelConnections.has(`sidepanel_${windowId}`);
    // console.log("isSidePanelOpen", open);
    return open;
};
