import { appearanceSettings, background_image_urls, beautifySettings, quickAccessSettings, historySettings, otherSettings, performanceSettings, pictureInPictureSettings, progressBarSettings, tabGroupsSettings, tabsSettings, bookmarkSettings } from "../configs/app"
import { sendRuntimeMessage } from "./browers/runtime";
import { getUserConfig, setUserConfig } from "./user";
import { userStore, userStoreCacheInstance } from "./stores/userStore"

const resetSettings = async (settings, settingsName) => {
    try {
        const { updateSetting } = userStore.getState();
        for (const [key, value] of Object.entries(settings)) {
            await updateSetting(key, value);
            sendRuntimeMessage("update_setting", { key, value });
        }
        return true;
    } catch (error) {
        console.error(`reset${settingsName} error`, error);
        return false;
    }
}

export const resetAppearanceSettings = async () => await resetSettings(appearanceSettings, 'AppearanceSettings');
export const resetTabsSettings = async () => await resetSettings(tabsSettings, 'TabsSettings');
export const resetBeautifySettings = async () => {
    await resetSettings(beautifySettings, 'BeautifySettings');
    await resetPresetBgImages();
};

export const resetPresetBgImages = async () => {
    const cacheInstance = await userStoreCacheInstance();
    const storedImages = await cacheInstance.get('background_image_urls') || [];
    const finalImages = Array.from(new Set([...background_image_urls, ...storedImages]));
    await cacheInstance.set('background_image_urls', finalImages);
}
export const resetHistorySettings = async () => await resetSettings(historySettings, 'HistorySettings');
export const resetPictureInPictureSettings = async () => await resetSettings(pictureInPictureSettings, 'PictureInPictureSettings');
export const resetTabGroupsSettings = async () => await resetSettings(tabGroupsSettings, 'TabGroupsSettings');
export const resetProgressBarSettings = async () => await resetSettings(progressBarSettings, 'progressBarSettings');
export const resetOtherSettings = async () => await resetSettings(otherSettings, 'OtherSettings');
export const resetPerformanceSettings = async () => await resetSettings(performanceSettings, 'PerformanceSettings');
export const resetBookmarkSettings = async () => await resetSettings(bookmarkSettings, 'bookmarkSettings');
export const resetQuickAccessSettings = async () => await resetSettings(quickAccessSettings, 'quickAccessSettings');

export const resetAllSettings = async () => {
    const results = {};
    results.appearance = await resetAppearanceSettings();
    results.tabs = await resetTabsSettings();
    results.beautify = await resetBeautifySettings();
    results.history = await resetHistorySettings();
    results.pictureInPicture = await resetPictureInPictureSettings();
    results.tabGroups = await resetTabGroupsSettings();
    results.other = await resetOtherSettings();
    results.progressBar = await resetProgressBarSettings();
    results.quickAccessSettings = await resetQuickAccessSettings();
    results.performance = await resetPerformanceSettings();
    results.bookmarkSettings = await resetBookmarkSettings();
    return results;
}


export const initSettings = async () => {
    const allSettings = { ...appearanceSettings, ...tabsSettings, ...beautifySettings, ...historySettings, ...pictureInPictureSettings, ...tabGroupsSettings, ...progressBarSettings, ...performanceSettings, ...otherSettings, ...quickAccessSettings, ...bookmarkSettings };
    let storedSettings = await getUserConfig("user_settings");
    if (!storedSettings) {
        if (!('sidePanel' in chrome)) {
            await chrome.action.setPopup({ popup: "popup.html" })
        } else {
            await chrome.sidePanel.setPanelBehavior({ openPanelOnActionClick: true }).catch((error) => console.error(error));
        }
        storedSettings = {}
    }
    const { updateSetting } = userStore.getState();

    for (const [key, value] of Object.entries(allSettings)) {
        // console.log("initSettings", key, value);
        if (!(key in storedSettings) || (typeof storedSettings?.[key] === 'object' && Object.keys(storedSettings?.[key]).length === 0)) {
            if (typeof value === 'object' && !Array.isArray(value)) {
                let newValue = {}
                for (const [k, v] of Object.entries(value)) {
                    if (!storedSettings[key] || !(k in storedSettings[key])) {
                        newValue[k] = v;
                    }
                }
                if (Object.keys(newValue).length > 0) {
                    await updateSetting(key, { ...storedSettings[key], ...newValue });
                    await sendRuntimeMessage("update_setting", { key: key, value: { ...storedSettings[key], ...newValue } });
                }
            } else {
                await updateSetting(key, value);
                await sendRuntimeMessage("update_setting", { key: key, value });
            }

        }
    }
    resetPresetBgImages();
    console.log("initSettings", storedSettings);
}

export const registerOnInstall = async () => {
    const manifest = chrome.runtime.getManifest();
    let storedVersion = await getUserConfig("version");
    if (!storedVersion) {
        await chrome.tabs.create({ url: chrome.runtime.getURL("options.html"), active: true });

    }
    if (manifest.version !== storedVersion) {
        await setUserConfig("version", manifest.version);
        await setUserConfig("new_version_badge", true);
        await setUserConfig("new_changelog_badge", true);

    }
    console.log("manifest", manifest);
}
