import { getUserConfig } from "../user";
import { removeTabById } from "./tabs";



export const createTabSaveMemoryActionDelayAlarm = async (tabId) => {

    let { saveMemoryAction, discardInactiveTabsMinutes, closeInactiveTabsMinutes } = (await getUserConfig("user_settings")) || {};

    switch (saveMemoryAction) {
        case "close":
            chrome.alarms.create(`tab_delay_close_${tabId}`, { delayInMinutes: closeInactiveTabsMinutes });
            break;
        case "discard":
            chrome.alarms.create(`tab_delay_discard_${tabId}`, { delayInMinutes: discardInactiveTabsMinutes });
            break;
        default:
            break;
    }
}

export const removeTabSaveMemoryActionAlarm = async (tabId) => {
    await chrome.alarms.clear(`tab_delay_close_${tabId}`);
    await chrome.alarms.clear(`tab_delay_discard_${tabId}`);
}

export const initAlarmListener = () => {
    chrome.alarms.onAlarm.addListener(async (alarm) => {
        console.log("alarm", alarm);
        if (alarm.name.startsWith("tab_delay")) {
            const { saveMemoryAction } = await getUserConfig("user_settings");
            const [, , type, tabIdStr] = alarm.name.split("_");
            const tabId = parseInt(tabIdStr);
            const tab = await chrome.tabs.get(tabId);

            if (!tab || saveMemoryAction !== type) {
                await removeTabSaveMemoryActionAlarm(tabId);
                return;
            }

            if (tab.audible || tab.active || !tab.autoDiscardable) {
                await createTabSaveMemoryActionDelayAlarm(tabId);
                return;
            }

            switch (type) {
                case "close":
                    await removeTabById(tabId);
                    break;
                case "discard":
                    await chrome.tabs.discard(tabId);
                    break;
                default:
                    console.warn(`Unknown saveMemoryAction type: ${type}`);
            }
        }
    });
}