import { getAccessToken, getCurrentUserUUID } from "../core/user";

async function httpRequest({ url, method = 'GET', params = null, headers = {}, parseJSON = false, userAgent = null, useFormData = false, retries = 3 }) {
    if (!url) {
        return createErrorResponse('httpRequest URL Not Set', { url, method, params, headers, parseJSON, userAgent, useFormData });
    }

    const fetchOptions = await createFetchOptions(method, headers, params, useFormData, userAgent);

    for (let attempt = 0; attempt < retries; attempt++) {
        try {
            const response = await fetch(url, fetchOptions);

            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }

            const data = parseJSON ? await response.json() : await response.text();

            return createSuccessResponse(response.status, data, { url, method, params, headers, parseJSON, userAgent, useFormData });
        } catch (error) {
            console.error(`Error in HTTP request (attempt ${attempt + 1}):`, error);
            if (attempt === retries - 1) {
                return createErrorResponse(error.message, { url, method, params, headers, parseJSON, userAgent, useFormData });
            }
            await delay(100);
        }
    }
}

async function createFetchOptions(method, headers, params, useFormData, userAgent) {
    const fetchOptions = {
        method: method.toUpperCase(),
        headers: new Headers(headers),
        cache: 'no-cache'
    };
    const [uuid, token] = await Promise.all([getCurrentUserUUID(), getAccessToken()]);
    fetchOptions.headers.set('VERTITAB-UUID', uuid);
    fetchOptions.headers.set('VERTITAB-TOKEN', token);
    fetchOptions.headers.set('VERTITAB-TIME', Date.now());
    if (method.toUpperCase() === 'POST' && params) {
        if (useFormData) {
            const body = new FormData();
            Object.keys(params).forEach(key => body.append(key, params[key]));
            fetchOptions.body = body;
        } else {
            fetchOptions.headers.set('Content-Type', 'application/json');
            fetchOptions.body = JSON.stringify(params);
        }
    }

    if (userAgent) {
        fetchOptions.headers.set('User-Agent', userAgent);
    }

    return fetchOptions;
}

function createErrorResponse(error, requestParams) {
    return {
        requestParams,
        statusCode: null,
        data: null,
        error,
        ok: false
    };
}

function createSuccessResponse(statusCode, data, requestParams) {
    return {
        requestParams,
        statusCode,
        data,
        error: null,
        ok: true
    };
}

function delay(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}

export {
    httpRequest
}