import { initSettings, registerOnInstall } from '../../core/startup';
import { getCurrentUserUUID } from '../../core/user';

import { handleRuntimeMessages } from './handleRuntimeMessages';
import { handleActionIconClick } from './action';
import { handleCommands } from './commands';
import { handleOnStartup } from './startup';
import { listenSidePanelConnected } from './connectSidePanel';
import { handleTabEvents, initTabInfos } from './handleTabEvents';
import chromeStorage from '../../utils/chromeStorage';
import { initAlarmListener } from '../../core/browers/alarm';



listenSidePanelConnected();
handleTabEvents();
handleRuntimeMessages();
initTabInfos();
initAlarmListener();

chrome.action.onClicked.addListener(handleActionIconClick);
chrome.runtime.onInstalled.addListener(async (details) => {
    console.log('onInstalled', details);
    const localChromeStorage = await chromeStorage("local");
    switch (details.reason) {
        case "install": {
            const uuid = await localChromeStorage.get("uuid");
            console.log("oninstall uuid", uuid);
            if (!uuid) {
                await localChromeStorage.set("uuid", await getCurrentUserUUID());
            }
            // chrome.runtime.setUninstallURL('https://docs.google.com/forms/d/e/1FAIpQLScw0nAcGOEIe4Anfte6REcgQ2jxzsiLG8OAUs2KUrVMLId-0A/viewform?usp=sf_link');
            break;
        }
        case "update": {
            await getCurrentUserUUID();
            break;
        }
        default:
            break;
    }
    initSettings();
    registerOnInstall();

})

chrome.runtime.onStartup.addListener(handleOnStartup)

chrome.commands.onCommand.addListener(handleCommands);
