import { debounce, delay } from "lodash-es";;
import { clearTabMetadataCache, initTabMetadataCache, tabStore, setTabMetaCache, getTabMetadataCache } from "../../core/stores/tabStore";
import { faviconURL, getHostnameFromUrl, isValidUrl } from "../../utils/helpers";
import { formatTabData, handleThumbnailCreation, playingVideoSwitchPip } from '../../core/browers/tabs'
import { cleanHistoriesByHostname, historyStore } from "../../core/stores/historyStore";
import { openGroupAndCollapseOthers, tabJoinDomainGroup } from "../../core/browers/tabGroups";
import { getUserConfig } from "../../core/user";
import { isSidePanelOpen } from "./connectSidePanel";
import { createTabSaveMemoryActionDelayAlarm, removeTabSaveMemoryActionAlarm } from "../../core/browers/alarm";
import { sendRuntimeMessage, sendTabMessage } from "../../core/browers/runtime";
import { siteStore } from "../../core/stores/siteStore";
import { addCachedWindows } from "../../core/stores/snapshotStore";
import { getAllParentFolderNamesByUrl } from "../../core/stores/bookmarkStore";
import { chromeGroupAvailableColors } from "../../utils/colors";
import { clearCookiesfromUrl } from "../../core/browers/cookies";

const { getSiteConfig } = siteStore.getState();
const { getTabConfig, setTabConfig, initTabs } = tabStore.getState();

initTabs();

export const tabInfos = new Map();
const lastActiveTabIds = new Map();
const groupLastActiveTab = new Map();

export const initTabInfos = async () => {
    const openTabs = await chrome.tabs.query({ windowType: 'normal' });
    for (const openTab of openTabs) {
        const tabData = formatTabData(openTab);
        const cacheData = await getTabMetadataCache(openTab.id);
        tabInfos.set(openTab.id, { ...tabData, ...cacheData });
    }
}

export const getTabInfos = async () => {
    if (tabInfos.size === 0) {
        await initTabInfos();
    }
    return Array.from(tabInfos.values());
}

export const handleTabEvents = () => {
    chrome.tabs.onCreated.addListener(handleTabOnCreated);
    chrome.tabs.onUpdated.addListener(handleTabOnUpdated);
    chrome.tabs.onActivated.addListener(handleTabOnActivated);
    chrome.tabs.onRemoved.addListener(handleTabOnRemoved);
    chrome.tabs.onReplaced.addListener(handleTabOnReplaced);
    chrome.tabs.onMoved.addListener(handleTabOnMoved);
    chrome.tabs.onZoomChange.addListener(handleOnZoomChange);
    chrome.tabGroups?.onMoved.addListener(handleTabGroupOnUpdated);
    chrome.tabGroups?.onCreated.addListener(handleTabGroupOnCreated);
    chrome.tabGroups?.onUpdated.addListener(handleTabGroupOnUpdated);
    chrome.tabGroups?.onRemoved.addListener(handleTabGroupOnRemoved);
    chrome.windows.onRemoved.addListener(handleWindowsOnRemoved);
    chrome.windows.onFocusChanged.addListener(handleWindowsOnFocusChanged, { windowTypes: ["normal"] });
}

const thumbnailCaptured = debounce(async (tabId) => {
    const thumbnail = await handleThumbnailCreation(tabId);
    if (thumbnail) {
        await setTabMetaCache(tabId, "thumbnail", thumbnail);
        await tabStore.getState().setTabConfig(tabId, { thumbnail });
    }
}, 500);

const updateGroupTitle = async (newGroupId, title) => {
    const groupColor = chromeGroupAvailableColors[newGroupId % chromeGroupAvailableColors.length].tabGroupColor;
    await chrome.tabGroups?.update(newGroupId, { title, color: groupColor });
};

const handleAutoActivateTabGrouping = async (tabId) => {
    try {
        const settings = await getUserConfig('user_settings');
        if (!settings?.autoActivateTabGrouping) return;

        const tabData = tabInfos.get(tabId);
        if (tabData.groupId !== -1) return;

        const groupTabIds = [tabData.id];
        const newGroupId = await chrome.tabs.group({ tabIds: groupTabIds });

        switch (settings?.autoActivateTabGroupingName) {
            case 'domain':
                if (tabData.hostname) {
                    await updateGroupTitle(newGroupId, tabData.hostname);
                }
                break;
            case 'title':
                if (tabData.title && !tabData.url.endsWith("//newtab/")) {
                    await updateGroupTitle(newGroupId, tabData.title);
                }
                break;
            case 'none':
            default:
                break;
        }
    } catch (error) {
        console.error("handleAutoActivateTabGrouping error message ", error.message);
        if (error.message.includes("user may be dragging a tab")) {
            setTimeout(() => handleAutoActivateTabGrouping(tabId), 100);
        }
    }
};

const handleTabOnCreated = async (tab) => {
    console.log("handleTabOnCreated", { tab });
    const tabData = await formatTabData(tab);
    tabInfos.set(tab.id, { ...tabData, lastAccessed: Date.now(), createdTime: Date.now() });
    addCachedWindows();
    handleAutoActivateTabGrouping(tab.id);
    if (isSidePanelOpen(tab.windowId)) return;
    const settings = await getUserConfig('user_settings');

    if (settings?.autoCreateOrJoinSameDomainGroup) {
        await tabJoinDomainGroup(tab.id);
    }
    initTabMetadataCache(tab.id);
}

const handleTabOnUpdated = async (tabId, changeInfo, tab) => {
    const tabData = await formatTabData(tab);
    tabInfos.set(tab.id, tabData);
    const tabConfig = await getTabConfig(tabId);
    const tabConfigs = { ...tabConfig, ...tabData };
    await setTabConfig(tabId, tabConfigs);
    console.log("handleTabOnUpdated", { tabId, changeInfo, tab, tabInfos });

    if (changeInfo.title) {
        const { autoActivateTabGrouping, autoActivateTabGroupingName } = await getUserConfig('user_settings') || {};
        if (autoActivateTabGrouping && tab.groupId > 0 && autoActivateTabGroupingName === 'title') {
            const { title } = await chrome.tabGroups.get(tab.groupId) || {};
            if (!title) {
                await chrome.tabGroups?.update(tab.groupId, { title: changeInfo.title.substring(0, 24) });
            }
        }
    }

    if (changeInfo.url) {
        const settings = await getUserConfig('user_settings');
        const siteConfig = await getSiteConfig(tabData.hostname);
        console.debug("changeInfo url siteConfig", siteConfig);

        if (siteConfig?.incognito === true) {
            setTimeout(async () => {
                await sendTabMessage(tab.id, 'clear_browsing_data', { options: siteConfig?.incognitoOptions || {} });
                if (siteConfig?.incognitoOptions?.cookies) await clearCookiesfromUrl(tab.url, siteConfig?.incognitoOptions?.skipAccountCookies);
                if (siteConfig?.incognitoOptions?.history) await cleanHistoriesByHostname(tabData.hostname)
            }, 1000)
        } else {
            // Record URL history
            let urlHistories = tabConfig?.urlHistories ? [...tabConfig.urlHistories] : [];
            const newUrlEntry = {
                tabId: tab.id,
                title: tab.title || tabConfig.title,
                url: tab.url,
                favIconUrl: tab?.favIconUrl || tabConfig?.favIconUrl || faviconURL(tab.url),
                active: true
            };

            const isDuplicate = urlHistories.some(entry => entry.url === tab.url);
            if (!isDuplicate) {
                urlHistories = urlHistories.map(entry => ({ ...entry, active: false }));
                urlHistories.push({ ...newUrlEntry, createdAt: Date.now(), lastAccessed: Date.now() });
            } else {
                // Update the existing entry to be active
                urlHistories = urlHistories.map(entry =>
                    entry.url === tab.url ? {
                        ...entry,
                        active: true,
                        lastAccessed: Date.now(),
                        ...(tab.favIconUrl && { favIconUrl: tab.favIconUrl }),
                        ...(tab.title && { title: tab.title })
                    } : { ...entry, active: false }
                );
            }

            urlHistories.sort((a, b) => a.lastAccessed - b.lastAccessed);
            await setTabConfig(tabId, { urlHistories });
            await setTabMetaCache(tabId, 'urlHistories', urlHistories);

        }

        sendTabMessage(tabId, 'tab_url_updated');



        if (siteConfig?.groupName) {
            const existGroup = await chrome.tabGroups?.query({ title: siteConfig?.groupName, windowId: tab.windowId });
            if (!existGroup.length) {
                const groupId = await chrome.tabs.group({ tabIds: [tabId], createProperties: { windowId: tab.windowId } });
                await chrome.tabGroups?.update(groupId, { title: siteConfig?.groupName, color: siteConfig?.groupColor ?? chromeGroupAvailableColors[groupId % chromeGroupAvailableColors.length].color, collapsed: true });
            } else {
                const existGroupId = existGroup[0].id;
                await chrome.tabs.group({ tabIds: [tabId], groupId: existGroupId });
            }
        } else if (settings?.useBookmarkFolderNameAsGroupName) {
            const folders = await getAllParentFolderNamesByUrl(changeInfo.url);
            let title;
            switch (settings.groupSelectBookmarkFolderLevel) {
                case 'top':
                    title = folders[0];
                    break;
                case 'parent':
                    title = folders[folders.length - 1];
                    break;
                case 'full':
                default:
                    title = folders.join('>');
                    break;
            }
            if (folders.length) {
                let groupId;
                const tabGroups = await chrome.tabGroups?.query({ title }) || [];
                const matchingGroup = tabGroups.find(group => group.title === title);
                if (matchingGroup && matchingGroup.id) {
                    groupId = matchingGroup.id;
                    await chrome.tabs.group({ tabIds: [tabId], groupId });
                } else {
                    groupId = await chrome.tabs.group({ tabIds: [tabId] });
                    if (chrome.tabGroups && typeof chrome.tabGroups.update === 'function') {
                        await chrome.tabGroups.update(groupId, {
                            title,
                            color: chromeGroupAvailableColors[tabId % chromeGroupAvailableColors.length].tabGroupColor
                        });
                    }
                }
            }
        } else {
            if (settings?.autoCreateOrJoinSameDomainGroup) {
                await tabJoinDomainGroup(tabId);
            }
        }
    }

    if (isSidePanelOpen(tab.windowId)) return;

    if (changeInfo.status === "complete" && tab.active && isValidUrl(tab.url)) {

        const siteConfig = await getSiteConfig(tabData.hostname);
        const matchedOption = siteConfig?.linkRefreshOptions.find(option => {
            try {
                return new RegExp(option.regex).test(tab.url);
            } catch {
                return tab.url.includes(option.regex);
            }
        });
        if ((siteConfig?.autoDiscardable === false || matchedOption) && tab.autoDiscardable === true) {
            await chrome.tabs.update(tabId, { autoDiscardable: false });
        } else {
            await chrome.tabs.update(tabId, { autoDiscardable: true });
        }


        delay(thumbnailCaptured, 500, tabId);
    }
}

let handleTabGroupCollapseLock = 0;

const handleTabGroupCollapse = async (groupId, windowId) => {
    const now = Date.now();
    if (now < handleTabGroupCollapseLock) return;
    handleTabGroupCollapseLock = now + 100;

    const { tabGroupCollapse } = await getUserConfig('user_settings') || {};

    console.log("handleTabGroupCollapse", { tabGroupCollapse, groupId, windowId })

    const group = await chrome.tabGroups.get(groupId);

    const highlightTab = async (tabId) => {
        const tabInfo = tabInfos.get(tabId) || (await chrome.tabs.query({ groupId }))[0];
        chrome.tabs.highlight({ tabs: tabInfo.index, windowId: tabInfo.windowId });
    };

    const updateGroupAndHighlightTab = async (collapsed) => {
        await chrome.tabGroups?.update(groupId, { collapsed });
        const lastActiveTabIdInGroup = groupLastActiveTab.get(groupId);
        await highlightTab(lastActiveTabIdInGroup);
    };

    switch (tabGroupCollapse) {
        case 'disable':
            if (group.collapsed) {
                await updateGroupAndHighlightTab(false);
            }
            break;
        case 'collapse_others':
            if (!group.collapsed) {
                await openGroupAndCollapseOthers(groupId, windowId);
                await updateGroupAndHighlightTab(false);
            } else {
                delay(async (windowId) => {
                    const expandGroups = await chrome.tabGroups.query({ collapsed: false, windowId });
                    if (expandGroups.length === 0) {
                        await updateGroupAndHighlightTab(false);
                    }
                }, 100, windowId);
            }
            break;
        case 'auto':
        default:
            break;
    }
};

const handleTabOnActivated = async (activeInfo) => {
    console.log("handleTabOnActivated", { activeInfo });

    const currentActiveTabId = lastActiveTabIds.get(activeInfo.windowId);
    lastActiveTabIds.set(activeInfo.windowId, activeInfo.tabId);

    if (currentActiveTabId !== activeInfo.tabId) {
        const settings = await getUserConfig('user_settings');
        if (settings?.tabSwitchPipBackground) {
            await playingVideoSwitchPip(currentActiveTabId, 'tab', 'playing_video_enterPictureInPicture')
        }
        tabInfos.set(currentActiveTabId, { ...tabInfos.get(currentActiveTabId), active: false, highlighted: false });

    }

    const tabInfo = tabInfos.get(activeInfo.tabId) || {};
    tabInfos.set(activeInfo.tabId, { ...tabInfo, active: true, highlighted: true });
    console.log("handleTabOnActivated tabInfo", tabInfos.get(activeInfo.tabId));

    if (tabInfo.groupId > 0) {
        groupLastActiveTab.set(tabInfo.groupId, activeInfo.tabId);
    }

    delay(async (tabInfo) => {
        // await handleTabGroupCollapse(tabInfo.groupId, activeInfo.windowId);
    }, 100, tabInfo);

    createTabSaveMemoryActionDelayAlarm(activeInfo.tabId);
    if (isSidePanelOpen(activeInfo.windowId)) return;

    await setTabMetaCache(activeInfo.tabId, 'lastAccessed', Date.now());
    delay(thumbnailCaptured, 500, activeInfo.tabId);
};

const handleTabOnRemoved = (tabId, removeInfo) => {
    console.log("handleTabOnRemoved", { tabId, removeInfo });
    tabInfos.delete(tabId);
    removeTabSaveMemoryActionAlarm(tabId);
    if (isSidePanelOpen(removeInfo.windowId)) return;

    getUserConfig('user_settings').then(settings => {
        if (settings?.enableHistory) {
            const tabConfig = tabInfos.get(tabId);
            if (tabConfig) {
                historyStore.getState().addHistory(tabConfig);
                tabInfos.delete(tabId);
            }
        }
        clearTabMetadataCache(tabId);
    });
}

const handleTabOnReplaced = async (addedTabId, removedTabId) => {
    console.log("handleTabOnReplaced", { addedTabId, removedTabId });

    await clearTabMetadataCache(removedTabId);
    tabInfos.delete(removedTabId);
    await initTabMetadataCache(addedTabId);

    const tab = await chrome.tabs.get(addedTabId);
    const tabData = await formatTabData(tab);
    tabInfos.set(tab.id, { ...tabData, lastAccessed: Date.now(), createdTime: Date.now() });

    const settings = await getUserConfig('user_settings');
    if (settings?.autoCreateOrJoinSameDomainGroup) {
        await tabJoinDomainGroup(addedTabId);
    }
}

const handleTabOnMoved = (tabId, moveInfo) => {
    tabStore.getState().loadTabs();
    handleAutoActivateTabGrouping(tabId);
}

const handleOnZoomChange = async (ZoomChangeInfo) => {
    console.log("handleOnZoomChange", { ZoomChangeInfo });
    tabStore.getState().setTabConfig(ZoomChangeInfo.tabId, { zoom: ZoomChangeInfo.newZoomFactor, zoomSettings: ZoomChangeInfo.zoomSettings });
};

const handleTabGroupOnCreated = async (group) => {
    console.log("handleTabGroupOnCreated", group);

    await tabStore.getState().loadGroups();
    addCachedWindows();

    const [firstGroupTab] = await chrome.tabs.query({ groupId: group.id });
    chrome.tabs.highlight({ tabs: firstGroupTab.index, windowId: firstGroupTab.windowId });
    groupLastActiveTab.set(group.id, firstGroupTab.id);

}

const handleTabGroupOnUpdated = async (group) => {
    console.log("handleTabGroupOnUpdated", group);

    await tabStore.getState().loadGroups();
    addCachedWindows();

    console.log("handleTabGroupOnUpdated get groupLastActiveTab", group.id, groupLastActiveTab);

    await handleTabGroupCollapse(group.id, group.windowId);

    await handleTabGroupCollapse(group.id, group.windowId);

    const { customGroupNames, customGroupRemember } = await getUserConfig('user_settings');

    if (customGroupNames.some(customGroup => customGroup.name === group.name) && customGroupRemember) {
        const groupTabs = await chrome.tabs.query({ groupId: group.id, windowId: group.windowId });
        for (const groupTab of groupTabs) {
            const hostname = getHostnameFromUrl(groupTab.url);
            await siteStore.getState().setSiteConfig(hostname, { groupName: group.name, groupColor: group.color });
            await sendRuntimeMessage('update_site_config', { hostname, config: { groupName: group.name, groupColor: group.color } });
        }
    }

}

const handleTabGroupOnRemoved = async (group) => {
    console.log("handleTabGroupOnRemoved", group);
    await tabStore.getState().closeGroup(group.id);
    addCachedWindows();
    groupLastActiveTab.delete(group.id);
}

const handleWindowsOnRemoved = (windowId) => {
    console.log('windows onRemoved', windowId);
    chrome.tabs.query({ windowType: "normal", windowId }).then(tabs => {
        const openTabIds = tabs.map(t => t.id);
        for (const openTabId of openTabIds) {
            clearTabMetadataCache(openTabId);
            removeTabSaveMemoryActionAlarm(openTabId);
        }
    });
}

const handleWindowsOnFocusChanged = async (windowId) => {
    console.log('windows OnFocusChanged', windowId);
}



