import { sendRuntimeMessage, sendTabMessage } from "../../core/browers/runtime";
import { isSidePanelOpen } from "./connectSidePanel";

export const handleCommands = (command) => {
    switch (command) {
        case 'openSidePanel':
            chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
                const activeTabId = tabs[0].id;
                const windowId = tabs[0].windowId;
                if (isSidePanelOpen(windowId)) {
                    sendRuntimeMessage('close_sidepanel');
                } else {
                    chrome.sidePanel.open({ tabId: activeTabId });
                }
            });
            break;
        case 'openPopup':
            chrome.action.setPopup({ popup: "popup.html" });
            chrome.action.openPopup();
            break;
        case 'switchPIP':
            chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
                const activeTabId = tabs[0].id;
                sendTabMessage(activeTabId, 'enterPictureInPicture');
            });
            break;
    }
}