


export const handleActionIconClick = (tab) => {
    // console.log('onClicked', tab)
    if ('sidePanel' in chrome) {
        chrome.windows.create({
            url: chrome.runtime.getURL('popup.html#/clickIcon'),
            type: 'popup',
            width: 300,
            height: 300,
            tabId: tab.id
        })
    } else {
        chrome.action.setPopup({ popup: "popup.html" })
    }


}
