import { act } from "react";
import { groupTabsByDomain, sortTabsByDomain, sortTabsByURL } from "../../core/browers/tabGroups";
import { playingVideoSwitchPip, removeTabById, saveTabPictureInPictureInfo, sendMessageToAllTabs, setTabActive } from "../../core/browers/tabs";
import { siteStore } from "../../core/stores/siteStore";
import { setTabMetaCache, tabStore } from "../../core/stores/tabStore";
import userStore from "../../core/stores/userStore";
import { sampleGroupColor } from "../../utils/colors";
import { getDomainFromUrl, getHostnameFromUrl } from "../../utils/helpers";
import { httpRequest } from "../../utils/http";
import { isSidePanelOpen } from "./connectSidePanel";
import { tabInfos } from "./handleTabEvents";
import { sendRuntimeMessage, sendTabMessage } from "../../core/browers/runtime";
import { memoryStore } from "../../utils/memoryStore";


let tabTempConfig = new Map();
let quickAccessSets = new Set();
userStore.getState().initUserStore();
export const handleRuntimeMessages = () => {
    chrome.runtime.onMessage.addListener((message, sender, sendResponse) => {
        console.log("chrome runtime onMessage:" + message?.action, { message, sender })
        asyncProcessMessage(message, sender, sendResponse);
        processOptionalMessage(message, sender, sendResponse);
        return true;
    });
}

async function asyncProcessMessage(message, sender, sendResponse) {
    try {
        const data = message.data
        let responseData = {}
        let tabs
        let tabQuery = {}
        switch (message.action) {
            case 'page_picture_in_picture':
                if (sender.tab?.id) {
                    await saveTabPictureInPictureInfo(sender.tab.id, sender.frameId, message.data)
                }
                sendResponse({ success: true })
                break;
            case 'close_other_tabs': {
                const keepTabIds = [...(data.tabIds || []), data.tabId]
                tabQuery = { windowType: 'normal', pinned: false };
                if (data.windowId) {
                    tabQuery.windowId = data.windowId
                }
                const closeOthertabs = await chrome.tabs.query(tabQuery)
                for (let tab of closeOthertabs) {
                    if (!keepTabIds.includes(tab.id)) {
                        await removeTabById(tab.id)
                    }
                }
                sendResponse({ success: true, data: responseData })
                break;
            }
            case 'close_same_domain_tabs': {
                tabQuery = { pinned: false, url: `${data.origin}/*` }
                if (data.windowId) {
                    tabQuery.windowId = data.windowId
                }
                tabs = await chrome.tabs.query(tabQuery)
                console.log('close_same_domain_tabs', tabs)
                for (let tab of tabs) {
                    await removeTabById(tab.id)
                }
                sendResponse({ success: true, data: responseData })
                break;
            }
            case 'close_same_domain_other_tabs': {
                tabQuery = { pinned: false, url: `${data.origin}/*` }
                if (data.windowId) {
                    tabQuery.windowId = data.windowId
                }
                tabs = await chrome.tabs.query(tabQuery)
                for (let tab of tabs) {
                    if (tab.id === data.tabId) continue;
                    await removeTabById(tab.id)
                }
                sendResponse({ success: true, data: responseData })
                break;
            }
            case 'bulk_close_tabs': {
                for (let tabId of data.tabIds) {
                    await removeTabById(tabId)
                }
                sendResponse({ success: true, data: responseData })
                break;
            }
            case 'chrome_delete_group': {
                try {
                    const toDeletedgroupTabs = await chrome.tabs.query({ windowType: 'normal', groupId: data.groupId })
                    chrome.tabs.remove(toDeletedgroupTabs.map(tab => tab.id));
                    sendResponse({ success: true, data: responseData })
                } catch (error) {
                    console.error("Error in chrome_delete_group:", error);
                    sendResponse({ success: false, error: error.message });
                }
                break;
            }
            case 'add_tabs_to_group': {
                chrome.tabs.group({ tabIds: data.tabIds }, groupId => {
                    chrome.tabGroups?.update(groupId, { title: data.title, color: data.color || sampleGroupColor() })
                })
                sendResponse({ success: true, data: responseData })
                break;
            }
            case 'ungroup': {
                try {
                    await chrome.tabs.ungroup(data.tabIds);
                    sendResponse({ success: true, data: responseData });
                } catch (error) {
                    console.error("Error in ungroup:", error);
                    sendResponse({ success: false, error: error.message });
                }
                break;
            }
            case 'chrome_ungroup': {
                try {
                    const groupTabs = await chrome.tabs.query({ windowType: 'normal', groupId: data.groupId });
                    if (groupTabs.length > 0) {
                        await chrome.tabs.ungroup(groupTabs.map(tab => tab.id));
                    }
                    sendResponse({ success: true, data: responseData });
                } catch (error) {
                    console.error("Error in chrome_ungroup:", error);
                    sendResponse({ success: false, error: error.message });
                }
                break;
            }
            case 'move_group_to_new_window': {
                chrome.windows.create({ type: 'normal' }, window => {
                    chrome.tabGroups?.move(data.groupId, { index: -1, windowId: window.id }).then(() => {
                        chrome.tabs.query({ windowId: window.id, index: 0 }).then(tabs => {
                            removeTabById(tabs.map(tab => tab.id)[0])
                        })
                    })
                })
                sendResponse({ success: true, data: responseData })
                break;
            }
            case 'move_tabs_to_window': {
                chrome.tabs.move(data.tabIds, { index: -1, windowId: data.windowId })
                sendRuntimeMessage('move_tabs_to_window', { ...data })
                sendResponse({ success: true })
                break;
            }
            case 'move_tabs_to_new_window': {
                chrome.windows.create({ type: 'normal' }, window => {
                    chrome.tabs.move(data.tabIds, { index: -1, windowId: window.id }).then(() => {
                        chrome.tabs.query({ windowId: window.id, index: 0 }).then(tabs => {
                            removeTabById(tabs.map(tab => tab.id)[0])
                        })
                    })
                })
                sendRuntimeMessage('move_tabs_to_new_window', { ...data })
                sendResponse({ success: true, data: responseData })
                break;
            }
            case 'group_tabs_by_domain': {
                try {
                    await groupTabsByDomain(data.windowId);
                    sendResponse({ success: true, data: responseData });
                } catch (error) {
                    console.error("Error in group_tabs_by_domain:", error);
                    sendResponse({ success: false, error: error.message });
                }
                break;
            }
            case 'sort_tabs_by_domain': {
                await sortTabsByDomain(data.windowId)
                sendResponse({ success: true, data: responseData })
                break;
            }
            case 'sort_tabs_by_url': {
                await sortTabsByURL(data.windowId)
                sendResponse({ success: true, data: responseData })
                break;
            }
            case 'discard_inactive_tabs': {
                tabQuery = { windowType: 'normal', active: false, discarded: false, pinned: false };
                if (data.windowId) {
                    tabQuery.windowId = data.windowId
                }
                tabs = await chrome.tabs.query(tabQuery);
                for (let tab of tabs) {
                    chrome.tabs.discard(tab.id);
                }
                sendResponse({ success: true, data: responseData })
                break;
            }
            case 'clear_tabs': {
                tabQuery = { windowType: 'normal', active: false, pinned: false };
                if (data.windowId) {
                    tabQuery.windowId = data.windowId
                }
                tabs = await chrome.tabs.query(tabQuery);
                for (let tab of tabs) {
                    await removeTabById(tab.id);
                }
                sendResponse({ success: true, data: responseData })
                break;
            }
            case 'close_all_discarded_tabs': {
                tabQuery = { windowType: 'normal', active: false, discarded: true, pinned: false };
                if (data.windowId) {
                    tabQuery.windowId = data.windowId
                }
                tabs = await chrome.tabs.query(tabQuery);
                for (let tab of tabs) {
                    removeTabById(tab.id);
                }
                sendResponse({ success: true, data: responseData })
                break;
            }
            case 'cancel_all_groups': {
                if (data.windowId) {
                    tabQuery.windowId = data.windowId
                }
                const tabGroups = await chrome.tabGroups?.query(tabQuery);
                const errors = [];
                for (const group of tabGroups) {
                    try {
                        const tabsInGroup = await chrome.tabs.query({ groupId: group.id });
                        await chrome.tabs.ungroup(tabsInGroup.map(tab => tab.id));
                    } catch (error) {
                        console.error(`Failed to ungroup tabs in group ${group.id}`, error);
                        errors.push(error.message);
                    }
                }
                if (errors.length > 0) {
                    sendResponse({ success: false, error: errors });
                } else {
                    sendResponse({ success: true, data: responseData });
                }
                break;
            }
            case 'updateSidebarWidth':
                userStore.getState().updateSetting('sidebarWidth', data.width);
                sendMessageToAllTabs("updateSidebarWidth", data);
                sendResponse({ success: true })
                break;
            case 'highlight_tab':
                await setTabActive(data.tabId);
                sendResponse({ success: true })
                break;
            case 'add_new_tab': {
                const createOptions = { active: true };
                if (data.url) {
                    createOptions.url = data.url;
                }
                const newTab = await chrome.tabs.create(createOptions);
                if (data.enterPictureInPicture) {
                    tabTempConfig.set(newTab.id, { ...(tabTempConfig.get(newTab.id) || {}), enterPictureInPicture: true })
                }
                sendResponse({ success: true, data: { newTab } })
                break;
            }
            case 'page_keep_alive':
                chrome.tabs.update(data.tabId, { autoDiscardable: false });
                sendTabMessage(data.tabId, "page_keep_alive");
                tabTempConfig.set(data.tabId, { ...(tabTempConfig.get(data.tabId) || {}), page_keep_alive: true })
                sendResponse({ success: true })
                break;
            case 'page_keep_alive_cancel':
                chrome.tabs.update(data.tabId, { autoDiscardable: true });
                sendTabMessage(data.tabId, "page_keep_alive_cancel");
                tabTempConfig.set(data.tabId, { ...(tabTempConfig.get(data.tabId) || {}), page_keep_alive: false })
                sendResponse({ success: true })
                break;
            case 'disable_sidebar':
                userStore.getState().updateSetting('sidebarEnabled', false);
                sendMessageToAllTabs("update_setting", { key: 'sidebarEnabled', value: false });
                sendResponse({ success: true })
                break;
            case 'update_sidebar_position':
                userStore.getState().updateSetting('sidebarPosition', data.position);
                sendMessageToAllTabs("update_setting", { key: 'sidebarPosition', value: data.position });
                sendResponse({ success: true })
                break;
            case 'update_site_config': {
                await siteStore.getState().setSiteConfig(message.data.hostname, message.data.config);
                sendResponse({ success: true })
                break;
            }
            case 'update_tab_title_options':
                await setTabMetaCache(data.tabId, "titleOptions", data.titleOptions);
                await tabStore.getState().setTabConfig(data.tabId, { titleOptions: data.titleOptions });
                break;
            case 'update_setting': {
                await userStore.getState().updateSetting(message.data.key, message.data.value);
                await sendMessageToAllTabs("update_setting", message.data)
                sendResponse({ success: true })
                break;
            }
            case "get_settings": {
                const settings = userStore.getState().settings;
                const hostname = getHostnameFromUrl(sender.tab?.url);
                const siteConfigs = await siteStore.getState().getSiteConfig(hostname);
                sendResponse({ success: true, data: { settings: settings, siteConfigs, tab: sender.tab, frameId: sender.frameId, hostname, tabConfig: tabTempConfig.get(sender.tab.id) } })
                break;
            }
            case "get_site_configs": {
                const hostname = getHostnameFromUrl(message.data?.url);
                const siteConfigs = await siteStore.getState().getSiteConfig(hostname);
                sendResponse({ success: true, data: { siteConfigs, hostname } })
                break;
            }
            case "get_pip_config": {
                try {
                    const domain = getDomainFromUrl(message.data.url);

                    const date = new Date();
                    const yymmdd = date.toISOString().slice(2, 10).replace(/-/g, '');
                    const yymmddhhss = date.toISOString().slice(2, 19).replace(/[-:T]/g, '');
                    const pipConfigUrl = `https://cfg.rabbitpair.com/configs/sites/${domain}/pip/config.json?t=${yymmdd}`;

                    // Check if we have a cached response
                    // const cacheKey = `pipConfig_${domain}`;
                    // const cachedResponse = memoryStore.get(cacheKey);
                    // if (cachedResponse) {
                    //     const cachedData = JSON.parse(cachedResponse);
                    //     const cacheTime = new Date(cachedData.timestamp);
                    //     const currentTime = new Date();
                    //     const hoursDifference = Math.abs(currentTime - cacheTime) / 36e5;

                    //     // If cached data is less than 4 hours old, use it
                    //     if (hoursDifference < 4) {
                    //         console.log("get_pip_config cache", cachedData);
                    //         sendResponse({ success: true, data: cachedData.response });
                    //         return; // Use return instead of break to exit the function
                    //     }
                    // }

                    const response = await httpRequest({ url: pipConfigUrl, method: "GET", parseJSON: true });
                    if (Array.isArray(response.data)) {
                        response.data.forEach(item => {
                            if (item.css && !item.css.startsWith('http')) {
                                item.css = `https://cfg.rabbitpair.com/configs/sites/${domain}/pip/${item.css}?t=${yymmdd}`;
                            }
                        });
                    } else if (response.data?.css && !response.data.css.startsWith('http')) {
                        response.data.css = `https://cfg.rabbitpair.com/configs/sites/${domain}/pip/${response.data.css}?t=${yymmdd}`;
                    }
                    // Cache the response for 4 hours
                    // const cacheData = {
                    //     timestamp: new Date().toISOString(),
                    //     response: response.data
                    // };
                    // memoryStore.set(cacheKey, JSON.stringify(cacheData));
                    console.log("get_pip_config response", response);
                    sendResponse({ success: true, ...response });
                } catch (error) {
                    console.error("Error fetching PIP config:", error);
                    sendResponse({ success: false, error: error.message });
                }
                break;
            }
            case "http_request": {
                try {
                    const response = await httpRequest(message.data);
                    sendResponse({ success: true, ...response });
                } catch (error) {
                    console.error("Error in HTTP request:", error);
                    sendResponse({ success: false, error: error.message });
                }
                break;
            }
            default:
                break;
        }


    } catch (e) {
        console.error(e)
        sendResponse({ success: false, error: e.message })
    }
}





export const processOptionalMessage = async (message, sender, sendResponse) => {


    if (isSidePanelOpen(sender.tab?.windowId)) {
        return;
    }
    let responseData = {}
    switch (message.action) {
        case "is_sidepanel_opened": {
            sendResponse({ success: true, data: { is_sidepanel_opened: isSidePanelOpen(sender.tab?.windowId) } })
            break;
        }
        case 'open_sidepanel': {
            if (sender.tab?.windowId && !isSidePanelOpen(sender.tab?.windowId)) {
                chrome.sidePanel?.setOptions({
                    enabled: true,
                    path: "sidepanel.html",
                })
                await chrome.sidePanel?.open({ tabId: sender.tab.id, windowId: sender.tab.windowId })
            }
            sendResponse({ success: true, data: responseData })
            break;
        }
        case 'open_popup': {
            const version = parseInt(/Chrome\/(\d+)/.exec(navigator.userAgent)[1], 10);
            if (version > 127) {
                chrome.action?.openPopup();
            }
            sendResponse({ success: true });
            break;
        }
        case 'page_video_intersection_changed': {
            if (sender.tab?.id) {
                if (message.data.isIntersecting && message.data.duration !== 'Infinity') {
                    await playingVideoSwitchPip(sender.tab.id, 'scroll', 'exitPictureInPicture')
                } else {
                    if (!message.data.paused && !message.data.muted) {
                        await playingVideoSwitchPip(sender.tab.id, 'scroll', 'playing_video_enterPictureInPicture')
                    }
                }
                await tabStore.getState().setTabConfig(sender.tab.id, { video_visiable: message.data.video_visiable });
            }
            sendResponse({ success: true })
            break;
        }

        case 'addQuickAccess': {
            const items = message.data.items;
            const storedQuickAccess = tabStore.getState().quickAccess;
            for (const item of storedQuickAccess) {
                quickAccessSets.add(item.url);
            }
            for (let i = 0; i < items.length; i++) {
                const item = items[i];
                if (storedQuickAccess.findIndex(q => q.url === item.url) === -1 && !quickAccessSets.has(item.url)) {
                    await tabStore.getState().setQuickAccess(crypto.randomUUID(), item)
                    quickAccessSets.add(item.url);
                }
            }
            sendResponse({ success: true })
            break;
        }
    }

}