import snapshotStore, { getCachedWindows } from "../../core/stores/snapshotStore";
import { localCacheMetadataInstance } from "../../core/stores/tabStore";



export const handleOnStartup = async () => {
    console.log("chrome runtime onStartup ");
    const cacheInstance = await localCacheMetadataInstance();
    cacheInstance.prune();
    getCachedWindows().then(cachedWindows => {
        console.log("cachedWindows", cachedWindows);
        if (cachedWindows) {
            snapshotStore.getState().addSnapshot(cachedWindows);
        }
    });
}